import './App.css';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { ToastContainer, toast } from "react-toastify";
import Login from './Component/Login'
import Reset from './Component/Login/reset';
import Register from './Component/Login/register'
import Profile from './Component/Profile';
import Messages from './Component/Messages';

toast.configure();

const App = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/web-chat/" element={<Navigate replace to="/web-chat/login" />} />
        <Route path="/web-chat/login" element={<Login />} />
        <Route path="/web-chat/reset" element={<Reset />} />
        <Route path="/web-chat/register" element={<Register />} />
        <Route path="/web-chat/messages" element={<Messages />} />
        <Route path="/web-chat/profile" element={<Profile />} />
      </Routes>
      <ToastContainer
        position="top-right"
        autoClose={3000}
      />
    </BrowserRouter>
  );
}

export default App;
